body {

  font-family: Helvetica, Arial, sans-serif, 
}
h1,h2,h3{
  margin:0.5rem 0;
}
h1{
  font-size: 1.5rem;
}
h2{
  font-size: 1.2rem;
}
h3{
  font-size: 1rem;
}
a{
  text-decoration: none;
}
.row{
  display: flex;
  /* border: 1px solid red; */
  justify-content: space-between;
}
.col-1{
  flex:1;
}
.col-2{
  flex:2;
}
.block{
  /* display: flex; */
  /* flex-flow: column wrap; */
  background-color: #e0e0e0;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.row.center{
  align-items: center;
}
img.small{
  /* display: flex;
  flex-direction: column; */
  max-height: 8rem;
  /* max-width: 250px; */
}
button {
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.5rem;
  border: 0.1rem #404040 solid;
  background-color: #2c4f9b;
  width: 8rem;
  cursor: pointer;
  color:aliceblue;
}
button.add {
  background-color: #3faa58;
  width: 1rem;
}
button.remove {
  background-color: #e44d4d;
  width: 1rem;
}
button.badge {
  background-color: #40aaf0;
  border: none;
  color: #ffffff;
  width: 1rem;
}
.text-right{
  text-align: right;
}
#basketMain{
  border: 2 px solid black;
}